import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import FormLayout from '../Component/FormLayout';
import InputBox from '../Component/InputBox';

export default class AdminSignUp extends Component{
    render() {
        const idIconStyle = {
            width: '17px',
            height: '17px',
            margin: '13px 0'
        };
        const passwordIconStyle = {
            width: '14px',
            height: '20px',
            margin: '14px 0'
        };

        return (
            <FormLayout pageName = "admin-signin"
                        imageName = "bg-picture-admin-signup.png">
                <div className="wrap-signin-form" style={{top: '40%'}}>
                    <h2>관리자 회원가입</h2>
                    <form>
                        <InputBox inputType="text" iconImage="ic-email.png" iconStyle={idIconStyle} placeholder="아이디를 입력하세요."/>
                        <InputBox inputType="password" iconImage="ic-lock.png" iconStyle={passwordIconStyle} placeholder="비밀번호를 입력하세요."/>
                        <InputBox inputType="text" iconImage="ic-person.png" iconStyle={passwordIconStyle} placeholder="이름을 입력하세요."/>
                        <InputBox inputType="text" iconImage="ic-work.png" iconStyle={passwordIconStyle} placeholder="소속을 입력하세요."/>
                        <InputBox inputType="tel" iconImage="ic-call.png" iconStyle={passwordIconStyle} placeholder="전화번호를 입력하세요."/>
                        <InputBox inputType="email" iconImage="ic-email.png" iconStyle={idIconStyle} placeholder="이메일을 입력하세요."/>
                        <button type="submit" className="btn-submit">회원가입</button>
                    </form>
                    <p className="no-account">
                        계정이 있으신가요? <Link to="/admin" style={{color: '#ffffff', fontWeight: 'bold'}}>로그인</Link>
                    </p>
                </div>
            </FormLayout>      
        )
    }
}


